  /* makes object of type INPUT (meaning choose file button) invisible */

  #input-file-upload {
    display: none;
  }
  
 
  /* adds pretty box around */
  #label-file-upload {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-width: 2px;
    border-radius: 2rem;
    border-style: solid;
    border-color: #cbd5e1;
    background-color: #f8fafc;
  }

  /* hover stuff  */
  #label-file-upload.hover{
    border: 3px solid #454cad;
    box-shadow: inset 0 0 0 6px #eee;
    transition: 0.5s;
  }
  
  #label-file-upload:hover{
    border-color: #454cad;
    transition: 0.5s;
  }
  
  #label-file-upload.drag-active {
    background-color: #ffffff;
  }

  /* idk what this does but its wrapped around the button */

  .upload-btn-wrapper {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
  }

  .upload-btn-wrapper input[type=file] {
    font-size: 100px;
